:root {
  --lime-primary: #e5f0b6;
  --dark-primary: #2f241f;
  --dark-light: #667085;
  --dark-light-2: #cfcfcf;
  --white-primary: #fff;
  --brown-primary: #817253;
  --blue-primary: #3570c9;
  --blue-light: #e4ecff;
  --red-primary: #ec1a5b;
  --red-light: #fceaee;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--lime-primary);
  color: var(--dark-primary);
  font-family: Inter, sans-serif;
  font-size: 1.2rem;
}

button {
  background: none;
  border: none;
  outline: none;
}

#app {
  background: var(--white-primary);
  width: 100%;
  max-width: 425px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 2rem 1rem 0;
  position: relative;
}

.card__header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.card__header span {
  color: var(--dark-light);
  font-size: 1rem;
  font-weight: 600;
}

.card__header a {
  color: var(--dark-light);
  font-size: .8rem;
  text-decoration: none;
}

nav {
  z-index: 100;
  background: var(--white-primary);
  width: 100%;
  max-width: 425px;
  margin-left: -1rem;
  position: fixed;
  bottom: 0;
  box-shadow: 1px 1px 10px #0000001a;
}

nav ul {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 1rem 2rem;
  list-style: none;
  display: flex;
}

nav ul li a {
  color: var(--dark-light-2);
  width: 32px;
  height: 32px;
  font-size: .8rem;
  text-decoration: none;
  display: inline-flex;
}

nav ul li a.nav__active {
  color: var(--blue-primary);
  position: relative;
}

nav ul li a.nav__active:after {
  content: "";
  background: var(--blue-primary);
  border-radius: 2em;
  width: 4px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  translate: -50% 50%;
}

